.head {
  height: 100%;
}

img {
  max-width: 100%;
  height: auto;
}

a {
  text-decoration: none;
  color: #000000;
}

ul {
  padding: 0;
  margin: 0;
}

li {
  list-style: none;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.circle-button {
  width: 6px;
  height: 6px;
  padding: 0;
  border-radius: 50%;
  border: none;
  background-color: rgba(40, 54, 69, 0.3);
}

.circle-button:hover,
.circle-button:focus {
  background-color: rgba(40, 54, 69, 0.6);
}

.circle-button:active {
  background-color: rgba(40, 54, 69, 0.1);
}

.circle-button--activ {
  background-color: #ffffff;
  width: 12px;
  height: 12px;
  padding: 0;
  border-radius: 50%;
  border: 3px solid #283645;
}

.body {
  min-width: 320px;
  margin: 0 auto;
  min-height: 100%;
  display: grid;
