    font-size: 18px;
    line-height: 18px;
    text-transform: none;
    display: inline-block;
    padding-left: 0;
    padding-right: 0;
  }
}
.nav__link:hover, .nav__link:focus {
  color: #d22856;
}
.nav__link:active {
  color: rgba(210, 40, 86, 0.3);
}

.header__button {
  position: absolute;
  top: 23px;
  right: 20px;
  width: 50px;
  height: 23px;
  cursor: pointer;
  border: none;
  background-color: transparent;
}
@media (min-width: 660px) {
  .header__button {
    top: 45px;
  }
}
@media (min-width: 1200px) {
  .header__button {
    display: none;
  }
}

@media (max-width: 1199px) {
  .header__nav--open .header__button::before, .header__nav--open .header__button::after {
    content: "";
    position: absolute;
    right: -2px;
    width: 28px;
    height: 2px;
    background-color: #ffffff;
  }
  .header__nav--open .header__button::before {
    transform: rotate(45deg);
  }
  .header__nav--open .header__button::after {
    transform: rotate(-45deg);
  }
  .header__nav--open .header__button:hover::before, .header__nav--open .header__button:hover::after, .header__nav--open .header__button:focus::before, .header__nav--open .header__button:focus::after {
    background-color: #d22856;
  }
  .header__nav--open .header__button:active::before, .header__nav--open .header__button:active::after {
    background-color: rgba(210, 40, 86, 0.3);
  }
  .header__nav--closed .header__button::before {
    content: "";
    position: absolute;
    right: 0;
    top: 0;
    width: 50px;
    height: 4px;
    background-color: #ffffff;
    border-radius: 5%;
    box-shadow: 0 9px 0 0 #ffffff, 0 18px 0 0 #ffffff;
  }
  .header__nav--closed .header__button:hover::before,
.header__nav--closed .header__button:focus::before {
    background-color: #d22856;
    box-shadow: 0 9px 0 0 #d22856, 0 18px 0 0 #d22856;
  }
  .header__nav--closed .header__button:active::before {
    background-color: rgba(210, 40, 86, 0.3);
    box-shadow: 0 9px 0 0 rgba(210, 40, 86, 0.3), 0 18px 0 0 rgba(210, 40, 86, 0.3);
  }
  .header__nav--nojs .header__button {
    display: none;
  }
  .header__nav--closed .nav__list {
    display: none;
  }
}
